/* body */
.body {
    justify-content: center;
    align-items: center;
    scroll-behavior: smooth;
}

/* home */
.home_title {
    font-size: 50px;
    font-weight: 800;
    margin-left: 350px;
    margin-top: 150px;
}

.home_title2 {
    font-size: 30px;
    font-weight: 500;
    margin-left: 350px;
    margin-top: 20px;
}

.home_profile {
    width: 300px;
    height: auto;
    margin-left: 40px;
    margin-top: 100px;
}

.home_line {
    display: inline-flex;
    margin: 0px auto;
}

.home_button {
    padding: 10px 20px 10px;
    margin-left: 350px;
}

.github_button {
    width: 47px;
    height: auto;
    margin-left: 60px; 
}

.button_line {
    display: inline-flex;
    margin-top: 25px;
}


/* about */
.about {
    font-size: 30px;
    font-weight: 800;
    text-align: center;
    margin-top: 1rem;
    margin-bottom: 5rem;
}

.about_profile {
    width: 250px;
    margin-right: 1rem; 
    margin-left: 30px;
    text-align: center;
    font-size: 18px;
    font-weight: 500;
}

.about_line3 {
    width: 250px;
    height: 250px;
    margin-right: 1rem; 
    margin-left: 150px;
    text-align: center;
    border-radius: 50%;
    background-color: rgb(67, 217, 217);
}

.about_right {
    display: inline-block;
    padding-left: 160px;
}

.about_tap {
    display: flex;
    margin-bottom: 16px;
    text-align: center;
    line-height: 30px;
}

.about_tap1 {
    border-radius: 10px;
    border: 0.1rem solid #25427b;
    width: 100px;
    height: 35px;
    background-color: rgb(255, 255, 255);
    box-shadow: 3px 3px 0 #333;
}

.about_tap1_1 {
    line-height: 40px;
}

.about_tap1_2 {
    border-radius: 10px;
    background-color: rgb(242, 242, 242);
    width: 210px;
    height: 35px;
    text-align: center;
    line-height: 34px;
}

.about_tap2 {
    border-radius: 10px;
    border: 0.1rem solid #25427b;
    width: 105px;
    height: 35px;
    margin-left: 10px;
    background-color: rgb(255, 255, 255);
    box-shadow: 3px 3px 0 #333;
}

.about_tap3 {
    border-radius: 10px;
    border: 0.1rem solid #25427b;
    width: 150px;
    height: 35px;
    margin-left: 10px;
    background-color: rgb(255, 255, 255);
    box-shadow: 3px 3px 0 #333;
}

.about_border {
    border-radius: 2.5rem;
    padding: 4rem;
    box-sizing: border-box;
    width: 530px;
    height: 22rem;
    display: flex;
    font-weight: 600;
    font-size: 17px;
    letter-spacing: 0.1rem;
    border: 0.2rem solid rgb(255, 255, 255);
    background-color: rgb(255, 255, 255);
}

.about_line {
    justify-content: space-between;
    margin: 0px auto;
    margin-top: 20rem;
    border-radius: 2rem;
    max-width: 1373px;
    height: 45rem;
    padding-top: 3rem;
    box-sizing: border-box;
    background-color: rgb(242, 242, 242);
    overflow: hidden;
    position: relative;
    box-shadow: 10px 10px 0 rgb(220, 219, 219);
}

.about_line2 {
    display: flex;
    flex-direction: row;
    align-items: center;
}

.about_border1 {
    border: 0.2rem solid rgb(37, 66, 123);
    border-radius: 20px;
    width: 530px;
    height: 330px;
    margin-left: 80px;
    background-color: rgb(255, 255, 255);
}

